import {
	getGoodsList,
	ztkLunbo,
	ztkLunboGoods,
	ztkTaobaoJX,
	pthd,
	ztkActivify,
	getSendCircleData,
	addSendCircle
} from '@/api/goods.js'
export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			autoplay: false,
			duration: 500,
			jumpover: '跳过',
			experience: '立即体验',
			tabBars: [{
				name: '全部',
				id: 'zb'
			}, {
				name: "母婴",
				id: "jp"
			}, {
				name: '美食',
				id: 'bh'
			}, {
				name: '女装',
				id: 'nz'
			}, {
				name: '美妆',
				id: 'lz'
			}, {
				name: '内衣',
				id: 'sp'
			}, {
				name: '百货',
				id: 'my'
			}, {
				name: '鞋包',
				id: 'mz'
			}, {
				name: '家电',
				id: 'xh'
			}, {
				name: '男装',
				id: 'ny'
			}, {
				name: '数码',
				id: 'sm'
			}, {
				name: '家电',
				id: 'jd'
			}, {
				name: '运动',
				id: 'yd'
			}, ],
		}
	},
	onLoad() {
		let i = 5;
		let timer = setInterval(() => {
			let v = i--;
			this.jumpover = "跳过" + v;
			if (v <= 0) {
				clearInterval(timer);
				uni.switchTab({
					url: '/pages/index/index'
				});
			}
		}, 1200)
		this.loadLazyData();
	},
	methods: {
		guidePage() {
			uni.redirectTo({
				url: '/pages/index/guide'
			});
			const value = uni.getStorageSync('launchFlag');
			console.log(value);
			if (value) {
				console.log(value);
				if (value == true) {
					uni.switchTab({
						url: '/pages/index/index'
					});
				} else {
					uni.redirectTo({
						url: '/pages/index/guide'
					});
				}
				uni.setStorage({
					key: 'launchFlag',
					data: false,
					success: function() {
						console.log('存储launchFlag:false');
					}
				});
			} else {
				console.log(value);
				uni.setStorage({
					key: 'launchFlag',
					data: true,
					success: function() {
						console.log('存储launchFlag');
					}
				});
				uni.redirectTo({
					url: '/pages/index/guide'
				});
			}
		},
		loadLazyData() {
			console.log(6);
			this.ztkLunbo();
			console.log(7);
			this.ztkActivify({
				type: "极品爆款"
			});
			this.ztkActivify({
				type: "天猫超市"
			});
			this.ztkActivify({
				type: "天猫国际"
			});
			console.log(8);
			this.ztkTaobaoJXData();
			console.log(9);
			uni.removeStorageSync("newsitems")
			this.randomfn();
		},
		ztkLunbo() {
			ztkLunbo().then(res => {
				console.log(res);
				uni.setStorageSync("ztkLunbo", res.content);
			})
		},
		ztkActivify(params) {
			console.log(params);
			switch (params.type) {
				case '极品爆款':
					ztkActivify({
						page: 1,
						type: params.type
					}).then(res => {
						if (res.status == 200) {
							uni.setStorageSync("goodsJpbk", res.content);
							console.log(res.content);
						}
					});
					break;
				case '天猫超市':
					ztkActivify({
						page: 1,
						type: params.type
					}).then(res => {
						if (res.status == 200) {
							uni.setStorageSync("goodsTmcs", res.content);
						}
					});
					break;
				case '天猫国际':
					ztkActivify({
						page: 1,
						type: params.type
					}).then(res => {
						if (res.status == 200) {
							uni.setStorageSync("goodsTmgj", res.content);
						}
					});
					break;
			}
		},
		ztkTaobaoJXData() {
			ztkTaobaoJX({
				page: 1,
			}).then(res => {
				uni.setStorageSync("tbjx", res);
				console.log(res);
			});
		},
		randomfn() {
			console.log("randomfn")
			let ary = [];
			for (let i = 0, length = this.tabBars.length; i < length; i++) {
				if (i < 1) {
					ary = [];
				} else {
					getGoodsList({
						page: 0,
						type: this.tabBars[i].name
					}).then(res => {
						console.log(this.tabBars[i].name);
						console.log(res.result);
						aryItem.data = res.result;
						ary.push(aryItem);
						if(i>=this.tabBars.length-1){
							uni.setStorageSync("newsitems",ary);
							console.log(JSON.stringify(ary));
						}
					});
				}
			}
			return ary;
		},
		launchFlag: function() {
			/**
			 * 向本地存储中设置launchFlag的值，即启动标识；
			 */
			uni.setStorage({
				key: 'launchFlag',
				data: true,
			});
			uni.switchTab({
				url: '/pages/index/index'
			});

		}
	}
}
